import React from 'react'
import Chart from 'react-apexcharts'
import { toast } from 'react-toastify';
import { getUserChatData } from '../redux/actions/admin.action';

export default class NewUsersGraphs extends React.Component {
        constructor(props) {
            super(props);
            this.state = {
                loading: true,
                options: {
                    chart: {
                        id: 'total-registered-users-per-month'
                    },
                    xaxis: {
                        categories: [1991, 1992, 1993, 1994, 1995, 1996, 1997, 1998, 1999]
                    }
                },
                series: [{
                    name: 'registered-users',
                    data: []
                }]
            }
        }

        componentDidMount(){
            const monthNames = ["January", "February", "March", "April", "May", "June",
                "July", "August", "September", "October", "November", "December"
            ];
            getUserChatData()
                .then(data =>{
                    let values ;
                    if(data !== undefined){
                        values = monthNames.map((month, index) => (
                            Math.max(...data.map((d) => (
                                 d.month === month ? d.numberOfUser : 0
                             )))
                         ))
                            
                    }else{
                        values = monthNames.map((month, index) => (
                            0
                        ))
                    }
                    this.setState(() => ({
                        options: {
                            ...this.state.options,
                            xaxis: {
                                categories: [...monthNames]
                            },
                        },
                        series: [{
                            name: 'series-1',
                            data: [...values]
                        }],
                        loading: false
                    }))
                   
                })
                .catch((error) =>{
                    toast.error(error)
                    this.setState((prevState) =>({
                    ...prevState,
                    loading: false
                }))
                })
                
        }

      
        render() {
            return ( 
                <div className='chart'>
                    {this.state.loading ?
                            <div className='loading__icon'/>
                         :
                        <React.Fragment>
                             <h3 className='heading-tertiary'>
                                Registered Users
                            </h3>
                            <Chart 
                                options = {this.state.options}
                                series = {this.state.series}
                                type = "bar"
                                width={400}
                                height={400}
                            />
                        </React.Fragment>
                    }
                   
                </div>
                
            )
        }
        }